"use strict";
var PostList = require('../post/postList');
var template = require('./searchTemplate');
var SearchComponent = (function () {
    function SearchComponent(triggerElemId) {
        var _this = this;
        this.triggerElem = document.getElementById(triggerElemId);
        this.triggerElem.addEventListener('click', function () { return _this.showSearchForm(); });
    }
    SearchComponent.prototype.showSearchForm = function () {
        if (this.containerElem) {
            this.containerElem.style.display = 'block';
            this.searchInputElem.focus();
        }
        else {
            this.containerElem = document.createElement('DIV');
            this.containerElem.innerHTML = template;
            document.body.insertBefore(this.containerElem, document.body.firstChild);
            this.bindSearchFormHandler();
            this.searchInputElem.focus();
        }
    };
    SearchComponent.prototype.bindSearchFormHandler = function () {
        var _this = this;
        this.endSearchElem = document.getElementById('endSearch');
        this.endSearchElem.addEventListener('click', function () { return _this.close(); });
        document.addEventListener('keyup', function (event) {
            if (event.keyCode === 27) {
                _this.close();
            }
        });
        this.searchButtonElem = document.getElementById('searchContainerButton');
        this.searchInputElem = document.getElementById('searchContainerInput');
        this.searchButtonElem.addEventListener('click', function () { return _this.triggerSearch(); });
        this.searchInputElem.addEventListener('keyup', function (event) {
            if (event.keyCode === 13) {
                _this.triggerSearch();
            }
        });
    };
    SearchComponent.prototype.close = function () {
        this.containerElem.style.display = 'none';
    };
    SearchComponent.prototype.triggerSearch = function () {
        var postList = new PostList;
        var searchString = this.searchInputElem.value;
        postList.searchForPosts(searchString);
        this.searchInputElem.value = '';
        this.close();
    };
    return SearchComponent;
}());
module.exports = SearchComponent;



