var CardTemplate = require('./cardTemplate');
var PostTemplate = require('./postTemplate');
var PostListTemplate = require('./postListTemplate');
var TemplateFactory = (function () {
    function TemplateFactory() {
    }
    TemplateFactory.prototype.getListTemplate = function (category) {
        switch (category) {
            case 'band':
                return new CardTemplate();
            default:
                return new PostListTemplate();
        }
    };
    TemplateFactory.prototype.getSingleTemplate = function (category) {
        switch (category) {
            case 'band':
                return new PostTemplate();
            default:
                return new PostTemplate();
        }
    };
    TemplateFactory.prototype.getListTemplateString = function (posts, category) {
        var template = this.getListTemplate(category);
        var templateString = '';
        templateString += template.preTemplate;
        templateString += this.compose(posts, template.template, category);
        templateString += template.postTemplate;
        return templateString;
    };
    TemplateFactory.prototype.getSingleTemplateString = function (post, category) {
        var template = this.getSingleTemplate(category);
        var templateString = '';
        templateString += template.preTemplate;
        templateString += template.template(post, category);
        templateString += template.postTemplate;
        return templateString;
    };
    TemplateFactory.prototype.compose = function (posts, templateFunction, category) {
        return posts.map(function (post) {
            return templateFunction(post, category);
        }).join('');
    };
    return TemplateFactory;
}());
module.exports = TemplateFactory;



